import Shere from "./shere-model.js";
const shere = new Shere()
export default {
	data() {
		return {
			userInfo: {},
		}
	},
	onLoad() {
		const that = this
		that._onLoad()
	},
	methods: {
		_onLoad(callBack) {
			const that = this
			that.userInfo = that.$store.state.userInfo;
			that.getDetailsData(() => {
				callBack && callBack();
			})
		},
		// 获取列表
		getDetailsData(callBack) {
			const that = this
			shere.getDetailsData({}, (res) => {
				if (res.status_code == 'ok') {
					let userInfo = that.$store.state.userInfo;
					that.userInfo = Object.assign(userInfo, res.data)
					that.$store.commit('updateUserInfo', that.userInfo);
				}
				callBack && callBack();
			})
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
	  var that = this;
	  that.page = 1;
	  that._onLoad(() => {
	    uni.stopPullDownRefresh();
	  });
	},
	// 分享
	onShareAppMessage() {
		let shareData = {
			title: '',
			path: `pages/index/index?${this.userInfo.id}`,
			imageUrl: ''
		}
		return shere.onShareAppMessage(shareData);
	}
